import React from 'react'
import { AppBar, Box, Divider, Drawer, IconButton, List, ListItem, ListItemButton, ListItemText, Toolbar, Typography } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { Link } from "react-router-dom";
function Header() {
    const [openDrawer, setopenDrawer] = React.useState(false); // used to open side menu
    const handleDrawerToggle = () => setopenDrawer(currentState => !currentState); // function that switch the side menu from opend to closed the the other way too
    const container = typeof window !== 'undefined' ? () => window.document.body : undefined;

    return (
        <>
            
            <AppBar component="nav">
                <Toolbar>
                    <IconButton color="inherit" aria-label='open drawer' edge="start" onClick={handleDrawerToggle} sx={{ mr: 2, display: { sm: 'none' } }}><MenuIcon /></IconButton>
                    <Typography variant="h6" component="div" sx={{ flexGrow: 1, display: { sx: 'none', sm: 'block' } }}>CHAT APP</Typography>
                    <Box>
                        <List sx={{ display: { xs: "none", sm: "flex" }}}>
                            <ListItem component={Link} to="/login">
                                <ListItemText primary="Login" sx={{color: "#fff"}}/>
                            </ListItem>
                            <ListItem component={Link} to="/register">
                                <ListItemText primary="Register" sx={{color: "#fff"}}/>
                            </ListItem>
                        </List>
                    </Box>
                </Toolbar>
            </AppBar>
            <nav>
                <Drawer
                    container={container}
                    variant="temporary"
                    open={openDrawer}
                    onClose={handleDrawerToggle}
                    ModalProps={{
                        keepMounted: true, // Better open performance on mobile.
                    }}
                    sx={{
                        display: { xs: 'block', sm: 'none' },
                        '& .MuiDrawer-paper': { boxSizing: 'border-box', width: 240 },
                    }}
                >
                    <Box onClick={handleDrawerToggle} sx={{ textAlign: 'center' }}>
                        <Typography variant="h6" sx={{ my: 2 }}>
                            CHAT APP
                        </Typography>
                        <Divider />
                        <List>
                            <ListItem disablePadding component={Link} to="/login">
                                <ListItemButton sx={{ textAlign: 'center' }}>
                                    <ListItemText primary="Login" />
                                </ListItemButton>
                            </ListItem>
                            <ListItem disablePadding component={Link} to="/register">
                                <ListItemButton sx={{ textAlign: 'center' }}>
                                    <ListItemText primary="Register" />
                                </ListItemButton>
                            </ListItem>
                        </List>
                    </Box>
                </Drawer>
            </nav>
        </>
    )
}

export default Header