import { createSlice } from "@reduxjs/toolkit";

export const chatSlice = createSlice({
    name: "chat",
    initialState: {
        messages: [],
    },

    reducers: {
        sendMessage: (state, action) => {
            console.log(`NEW MESSAGE ARRIVED : ${JSON.stringify(action.payload)}`)
            state.messages.push({...action.payload});
        },
        deleteMessage: (state, action) => {
            console.log(`MESSAGE WITH ID: ${action.payload} WILL BE DELETED`);
            state.messages = state.messages.filter(message => message.id !== action.payload);
        },
        allMessages: (state, action) => {
            console.log(`LIST OF MESSAGES HAS BEEN OVERRIDED BY ${JSON.stringify(action.payload)}`);
            state.messages = action.payload;
        }
        //to be continued
    }
});

export const {allMessages, sendMessage, deleteMessage} = chatSlice.actions;
export default chatSlice.reducer;