import "../../Register.css";
import React, {useEffect} from 'react';
import {useFormik} from "formik";
import {Grid, TextField, Typography, Button, Paper, Alert} from '@mui/material';
import CloudUploadIcon from "@mui/icons-material/CloudUpload";
import {useDispatch, useSelector} from "react-redux";
import LoadingView from "./LoadingView";
import {set_loading} from "../reducers/AppSlice";
import * as Yup from 'yup';
import axios from "axios";
function Register() {
    const isLoading = useSelector((state) => state.app.isLoading);
    const dispatch = useDispatch();

    const form = useFormik({
        initialValues: {
            username: "",
            password: "",
            profilePicture: undefined
        },
        validationSchema: Yup.object().shape({
            username: Yup.string()
                .required("You must enter a username!")
                .min(3, "Your username is too short, minimum accepted length is 3 characters.")
                .max(32, "Your username is too long, maximum accepted length is 32 characters.")
                .matches(/^[a-zA-Z0-9_]+$/ ,"Your username should contains only letters / numbers or underscore (_) only"),
            password: Yup.string()
                .required('Password is required')
                .min(8, 'Password must be at least 8 characters')
                .max(128, 'Password must be at most 128 characters')
                .matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[!@#$%^&*()_+=-{};:"<>,./?])[A-Za-z\d!@#$%^&*()_+=-{};:"<>,./?]{8,}$/, 'Password must contain at least one lowercase letter, one uppercase letter, one number, and one special character'),

        }),
        onSubmit: values => {
            //for real deployment change string comparison to development
            axios.post(`${process.env.NODE_ENV === "production" ? process.env.REACT_APP_API_DEVELOPMENT_URL : process.env.REACT_APP_API_PRODUCTION_URL}register`, values)
                .then(res => {
                    alert(res.data.status);
                })
                .catch(err => alert("something went wrong: \n"+JSON.stringify(err)));
        }

        //
    });

    const previewProfilePicture = (file) => {
        if (file) {
            const fileSizeLimit = 1024 * 1024 * 5; // 5MB
            if(file.size > fileSizeLimit) {
                form.setFieldError("profilePicture", `Image size is too large, maximum accepted size is ${fileSizeLimit / 1024 / 1024}MB.`)
                return;
            }
            const fileReader = new FileReader();
            fileReader.onloadend = function(event){
                const image = new Image();
                image.onload = function(){
                    if(image.width > 1024 || image.height > 768){
                        form.setFieldError("profilePicture", "Image size is too large, maximum size should be between 640x480 to 1024x768.");
                    }

                    image.src=event.target.result;
                }
                const preview = document.querySelector("#preview_profile_picture")
                if (preview && !form.errors.profilePicture) {
                    form.setFieldValue("profilePicture", fileReader.result);
                    preview.setAttribute("src", fileReader.result);
                } else {
                    alert("problem selecting img tag");
                }
            };
            fileReader.readAsDataURL(file);
        }
    }

    useEffect(() => {
        dispatch(set_loading(true));
         if (document.readyState === "complete") {
             dispatch(set_loading(false));
         }
        return () => dispatch(set_loading(true));
    }, [dispatch]);



    if (isLoading) {
        return <LoadingView/>
    } else {
        return (

            <>

                <Paper elevation={3} id="container">
                    <Typography variant="h5">Register Form</Typography>
                    <form onSubmit={form.handleSubmit} method="POST">
                        <Grid container spacing={2}>
                            <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                                <TextField
                                    id="username"
                                    name="username"
                                    variant="outlined"
                                    label="Username"
                                    placeholder="Please enter a unique username: "
                                    value={form.values.username}
                                    onChange={form.handleChange}
                                    onBlur={form.handleBlur}
                                    error={Boolean(form.errors.username)}
                                    helperText={form.errors.username}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                                <TextField
                                    type="password"
                                    id="password"
                                    name="password"
                                    placeholder="Please Enter a strong password: "
                                    variant="outlined"
                                    label="Password"
                                    value={form.values.password}
                                    onChange={form.handleChange}
                                    onBlur-={form.handleBlur}
                                    error={Boolean(form.errors.password)}
                                    helperText={form.errors.password}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                                <figure hidden={!form.values.profilePicture}>
                                    <img src="#" id="preview_profile_picture"
                                         alt="preview of your porfile"/>
                                    <figcaption>Profile Picture Preview</figcaption>
                                </figure>
                                {Boolean(form.errors.profilePicture) && <Alert variant="filled" severity="error">{form.errors.profilePicture}</Alert>}
                                <Button
                                    component="label"
                                    role={undefined}
                                    variant="outlined"
                                    tabIndex={-1}
                                    startIcon={<CloudUploadIcon/>}
                                    fullWidth
                                >
                                    {form.values.profilePicture ? 'Change' : 'Select'} Profile Picture
                                    <TextField inputProps={{ accept: 'image/jpeg, image/png, image/gif' }}  type="file"
                                               sx={{visibility: "hidden", position: "absolute"}}
                                               onChange={(e) => previewProfilePicture(e.currentTarget.files[0])}/>
                                </Button>
                            </Grid>
                            <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                                <Button variant="contained" type="submit">
                                    Register
                                </Button>
                            </Grid>
                        </Grid>
                    </form>
                </Paper>
            </>
        )
    }
}

export default Register