import {createSlice} from "@reduxjs/toolkit";

export const AppSlice = createSlice({
    name: "application",
    initialState: {
        loading: false,
    },
    reducers: {
        set_loading: (state, action) => {
            state.loading = action.payload;
        },
    }
});

export const {set_loading} = AppSlice.actions;

export default AppSlice.reducer;