import { createTheme } from '@mui/material/styles';

const lovelyTheme = createTheme({
    palette: {
        primary: {
            main: '#F7C67B', // Main Color
            contrastText: '#333333', // Text Color
        },
        secondary: {
            main: '#8BC34A', // Secondary Color
            contrastText: '#FFFFFF', // Text Color
        },
        background: {
            default: '#F2F2F2', // Background Color
        },
        text: {
            primary: '#333333', // Text Color
        },
        action: {
            active: '#FFC107', // Accent Color
        },
        // Additional Colors
        highlight: {
            main: '#FFFF99', // Highlight Color
        },
        shadow: {
            main: '#AAAAAA', // Shadow Color
        },
    },
    // Additional Theme Configurations
    typography: {
        fontFamily: 'Roboto, sans-serif',
        h1: {
            fontSize: '3rem',
            fontWeight: 700,
            color: '#333333', // Text Color
        },
        h2: {
            fontSize: '2.5rem',
            fontWeight: 700,
            color: '#333333', // Text Color
        },
        h3: {
            fontSize: '2rem',
            fontWeight: 700,
            color: '#333333', // Text Color
        },
        h4: {
            fontSize: '1.5rem',
            fontWeight: 700,
            color: '#333333', // Text Color
        },
        h5: {
            fontSize: '1.25rem',
            fontWeight: 700,
            color: '#333333', // Text Color
        },
        h6: {
            fontSize: '1rem',
            fontWeight: 700,
            color: '#333333', // Text Color
        },
    },
    components: {
        MuiButton: {
            styleOverrides: {
                root: {
                    borderRadius: '25px',
                    padding: '10px 20px',
                    fontSize: '1rem',
                    fontWeight: 700,
                    textTransform: 'none',
                    '&:hover': {
                        backgroundColor: '#FFC107', // Accent Color
                    },
                },
            },
        },
    },
});

export default lovelyTheme;