import React from 'react'
import {Box, Toolbar } from '@mui/material';
import {Provider} from "react-redux";
import store from "../store";
import { Outlet } from 'react-router-dom';
import "../styles/Body.css";
function Body() {
    return (
        <>
            <Box component="main">
                <Toolbar />
                <Provider store={store}>
                    <Outlet />
                </Provider>
            </Box>
        </>
    )
}

export default Body